}

.container {
  max-width: 1280px;
  margin-inline: auto;
  padding-inline: 55px;
}

@media (width <= 1023.98px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width <= 767.98px) {
  .container {
    padding-inline: 20px;
  }
}

.visually-hidden {
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

@media (width >= 1024px) {
  .hidden-desktop {
    display: none !important;
  }
}

html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  background-color: #0000;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="button"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="reset"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted buttontext;
}

[type="button"]:-moz-focusring {
  outline: 1px dotted buttontext;
}

[type="reset"]:-moz-focusring {
  outline: 1px dotted buttontext;
}

[type="submit"]:-moz-focusring {
  outline: 1px dotted buttontext;
}

fieldset {
  padding: .35em .75em .63em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  white-space: normal;
  max-width: 100%;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button {
  height: auto;
}

[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

.page {
  color: #0f0e08;
  scroll-behavior: smooth;
  font: 400 14px / 1.5 IBM Plex Sans, sans-serif;
}

.page__body {
  background-color: #fff;
  min-width: 320px;
  margin: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.icon {
  background-image: url("icon-burger-menu.d4b7c3e0.svg");
  background-size: cover;
  transition: transform .3s;
  display: block;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--burger-menu {
  z-index: 2;
  background-image: url("icon-burger-menu.d4b7c3e0.svg");
  grid-column: 1;
  width: 18px;
  height: 12px;
  margin-bottom: 143px;
  padding: 6px 3px;
}

@media (width <= 1023.98px) {
  .icon--burger-menu {
    margin-bottom: 64px;
  }
}

@media (width <= 767.98px) {
  .icon--burger-menu {
    margin-bottom: 270px;
  }
}

.icon--close {
  aspect-ratio: 1;
  background-image: url("icon-close.dddcf99e.svg");
  width: 14px;
  margin: 5px;
  display: inline-block;
}

.icon--facebook {
  background-image: url("icon-facebook.274d3661.svg");
}

.icon--instagram {
  background-image: url("icon-instagram.4226bbc5.svg");
}

.header__content {
  z-index: 1;
  --columns: 12;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 30px;
  height: 668px;
  display: grid;
  overflow: hidden;
}

@media (width <= 1023.98px) {
  .header__content {
    --columns: 6;
  }
}

@media (width <= 767.98px) {
  .header__content {
    --columns: 2;
    column-gap: 20px;
  }
}

@media (width <= 1023.98px) {
  .header__content {
    height: 399px;
  }
}

@media (width <= 767.98px) {
  .header__content {
    height: 502px;
  }
}

.header__top {
  z-index: 1;
  background-image: url("museum-header-img.e5eb93cf.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  grid-row: 1 / 2;
  grid-column-start: 6;
  width: 725px;
  height: 655px;
  display: grid;
  position: absolute;
  top: 0;
  right: 0;
}

@media (width <= 1023.98px) {
  .header__top {
    grid-column-start: 4;
    width: 374px;
    height: auto;
    min-height: 384px;
    max-height: 655px;
  }
}

@media (width <= 767.98px) {
  .header__top {
    width: 100%;
    min-height: 300px;
  }
}

.header__logo-link {
  z-index: 2;
  text-align: center;
  width: 170px;
  height: 72px;
  padding-top: 15px;
}

@media (width <= 1023.98px) {
  .header__logo-link {
    width: 220px;
  }
}

@media (width <= 767.98px) {
  .header__logo-link {
    width: 55px;
    height: 61px;
    padding: 0;
    position: absolute;
    top: 9px;
    left: 46%;
  }
}

.header__bottom {
  flex-direction: column;
  grid-column: 1 / 6;
  padding-top: 40px;
  display: flex;
}

@media (width <= 1023.98px) {
  .header__bottom {
    grid-column: 1 / 4;
  }
}

@media (width <= 767.98px) {
  .header__bottom {
    grid-column: 1 / -1;
    padding-top: 28px;
  }
}

.header__title {
  z-index: 10;
  text-align: left;
  color: #000;
  text-transform: uppercase;
  grid-column: 1 / 6;
  margin: 0 0 89px;
  font: 700 72px / 1 Montserrat, sans-serif;
}

@media (width <= 1023.98px) {
  .header__title {
    margin-bottom: 38px;
    font: 700 54px / 1.2 Montserrat, sans-serif;
  }
}

@media (width <= 767.98px) {
  .header__title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 45px;
  }
}

.header__button {
  z-index: 10;
  flex-direction: row;
  align-self: flex-end;
  place-items: center;
  width: 270px;
  display: flex;
}

@media (width <= 1023.98px) {
  .header__button {
    align-self: flex-start;
  }
}

@media (width <= 767.98px) {
  .header__button {
    width: 100%;
    max-width: 280px;
    margin: 0 auto;
  }
}

.header__button-text {
  box-sizing: border-box;
  color: #0f0e08;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #1a5a4c;
  width: 200px;
  height: 70px;
  font-size: 16px;
  font-weight: 500;
  line-height: 70px;
  text-decoration: none;
  transition-duration: .3s;
}

.header__button-text:hover {
  background-color: #d7dde3;
}

@media (width <= 767.98px) {
  .header__button-text {
    width: 230px;
    height: 50px;
    line-height: 50px;
  }
}

.header__events {
  display: block;
  position: relative;
  bottom: 15px;
}

@media (width <= 1023.98px) {
  .header__events {
    display: none;
  }
}

.header__events-link {
  color: #687480;
  text-transform: uppercase;
  writing-mode: vertical-rl;
  font-weight: 500;
  line-height: 1;
  text-decoration: none;
  transition-duration: .3s;
  transform: rotate(180deg);
}

.header__events-link:hover {
  color: #f14a27;
}

.header__events-link:before {
  content: "";
  background-color: #1a5a4c;
  width: 1px;
  height: 70px;
  position: absolute;
  top: -84px;
  right: 6px;
}

.button {
  aspect-ratio: 1;
  background: #1a5a4c url("keyboard-arrow-right.2b16c202.svg") center / 24px 24px no-repeat;
  border: none;
  height: 70px;
  transition-duration: .3s;
}

@media (width <= 767.98px) {
  .button {
    height: 50px;
  }
}

.button:hover, .button--red {
  background: #f14a27 url("arrow-right-black.0b8889b8.svg") center no-repeat;
}

.button--red:hover {
  background-color: #f66;
}

.events__wrapper {
  row-gap: 104px;
  display: grid;
}

@media (width <= 1023.98px) {
  .events__wrapper {
    row-gap: 64px;
  }
}

@media (width <= 767.98px) {
  .events__wrapper {
    row-gap: 60px;
  }
}

.events .event {
  --columns: 12;
  grid-template-columns: repeat(var(--columns), 1fr);
  align-items: center;
  column-gap: 30px;
  display: grid;
}

@media (width <= 1023.98px) {
  .events .event {
    --columns: 6;
  }
}

@media (width <= 767.98px) {
  .events .event {
    --columns: 2;
    column-gap: 20px;
  }
}

@media (width <= 1023.98px) {
  .events .event {
    row-gap: 40px;
  }
}

@media (width <= 767.98px) {
  .events .event {
    row-gap: 20px;
  }
}

.events .event__link {
  grid-column: span 7;
  align-items: center;
  margin: 0;
  text-decoration: none;
  display: grid;
}

@media (width <= 1023.98px) {
  .events .event__link {
    grid-column: span 6;
  }
}

@media (width <= 767.98px) {
  .events .event__link {
    grid-column: span 2;
  }
}

.events .event__image {
  object-fit: cover;
  width: 100%;
  height: 520px;
  transition: transform .3s;
}

.events .event__image:hover {
  transform: scale(1.05);
}

@media (width <= 767.98px) {
  .events .event__image {
    height: 210px;
  }
}

.events .event__info {
  flex-direction: column;
  grid-column: 9 / span 4;
  align-self: center;
  display: flex;
}

@media (width <= 1023.98px) {
  .events .event__info {
    grid-column: span 4;
  }
}

@media (width <= 767.98px) {
  .events .event__info {
    grid-column: span 2;
  }
}

.events .event__info-top {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  column-gap: 30px;
  margin-bottom: 30px;
  display: flex;
}

@media (width <= 767.98px) {
  .events .event__info-top {
    justify-content: space-between;
    margin-bottom: 10px;
  }
}

.events .event__info-top--banner {
  column-gap: 145px;
  margin-bottom: 16px;
  display: flex;
}

@media (width <= 1023.98px) {
  .events .event__info-top--banner {
    column-gap: 185px;
  }
}

@media (width <= 767.98px) {
  .events .event__info-top--banner {
    column-gap: 20px;
    margin-bottom: 20px;
    padding-inline: 20px;
  }
}

.events .event__info-category {
  text-transform: uppercase;
  color: #687480;
  margin: 0;
  font-weight: 500;
  line-height: 1;
}

@media (width <= 767.98px) {
  .events .event__info-category {
    font-size: 16px;
  }
}

.events .event__info-date {
  text-transform: uppercase;
  color: #1a5a4c;
  margin: 0;
  font-weight: 700;
  line-height: 1.5;
}

@media (width <= 767.98px) {
  .events .event__info-date {
    font-size: 16px;
  }
}

.events .event__info-title {
  color: #0f0e08;
  margin: 0 0 16px;
  font: 400 36px / 1 Montserrat, sans-serif;
}

@media (width <= 767.98px) {
  .events .event__info-title {
    font-size: 28px;
  }
}

.events .event__info-title:after {
  content: "";
  background-color: #f14a27;
  width: 15px;
  height: 15px;
  position: absolute;
  transform: translate(20px, 13px);
}

@media (width <= 767.98px) {
  .events .event__info-title:after {
    width: 10px;
    height: 10px;
    transform: translate(20px, 10px);
  }
}

.events .event__info-title--banner {
  text-align: left;
  margin: 0;
}

@media (width <= 767.98px) {
  .events .event__info-title--banner {
    text-align: center;
  }
}

.events .event__info-title--banner:after {
  content: none;
}

.events .event__info-description {
  color: #4e4e4e;
  margin: 0;
  font-size: 16px;
  font-weight: 300;
}

.events__banner {
  background-image: url("events-3.c94066a9.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 550px;
  margin-block: 104px;
  display: block;
  position: relative;
}

@media (width <= 1023.98px) {
  .events__banner {
    height: 390px;
    margin-block: 112px;
    margin-inline: 34px;
  }
}

@media (width <= 767.98px) {
  .events__banner {
    height: 210px;
    margin-block: 60px 82px;
    margin-inline: 0;
  }
}

.events__banner-info {
  background-color: #fff;
  border-bottom: 2px solid #1a5a4c;
  width: 570px;
  height: 204px;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
}

@media (width <= 1023.98px) {
  .events__banner-info {
    top: 77%;
  }
}

@media (width <= 767.98px) {
  .events__banner-info {
    width: 280px;
    height: 140px;
  }
}

.events__banner-content {
  flex-direction: column;
  justify-content: center;
  padding-block: 45px 50px;
  padding-inline: 100px;
  display: flex;
}

@media (width <= 767.98px) {
  .events__banner-content {
    padding-block: 20px;
    padding-inline: 0;
  }
}

.logo {
  color: #0f0e08;
  text-transform: uppercase;
  margin: 0;
  font: 700 36px / 1 Montserrat, sans-serif;
  text-decoration: none;
  transition: transform .3s;
}

.logo:hover {
  transform: scale(1.2);
}

.logo--mobile-size {
  font-size: 30px;
}

.footer__content {
  --columns: 12;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 30px;
  margin-bottom: 35px;
  display: grid;
}

@media (width <= 1023.98px) {
  .footer__content {
    --columns: 6;
  }
}

@media (width <= 767.98px) {
  .footer__content {
    --columns: 2;
    column-gap: 20px;
    margin-bottom: 65px;
  }
}

.footer__top {
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: space-between;
  align-items: flex-start;
  row-gap: 62px;
  width: 70px;
  height: 158px;
  display: flex;
}

@media (width <= 767.98px) {
  .footer__top {
    flex-direction: row;
    grid-column: 1 / -1;
    justify-content: space-between;
    align-items: center;
    width: 280px;
    height: 72px;
    margin-bottom: 40px;
  }
}

.footer__icons {
  flex-direction: row;
  column-gap: 22px;
  display: flex;
}

@media (width <= 1023.98px) {
  .footer__icons {
    column-gap: 32px;
  }
}

@media (width <= 767.98px) {
  .footer__icons {
    column-gap: 20px;
  }
}

.footer__schedule {
  color: #0f0e08;
  flex-direction: column;
  grid-area: 1 / 3 / 2 / 5;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 16px;
  width: 170px;
  margin-bottom: 87px;
  display: flex;
}

@media (width <= 1023.98px) {
  .footer__schedule {
    grid-area: 1 / 2 / 2 / 4;
    width: 220px;
    margin-bottom: 64px;
  }
}

@media (width <= 767.98px) {
  .footer__schedule {
    grid-area: 2 / 1 / 3 / 2;
    width: 130px;
    height: 365px;
    margin-bottom: 70px;
  }
}

.footer__contacts {
  color: #0f0e08;
  text-align: left;
  border-right: 1px solid #1a5a4c;
  grid-area: 1 / 5 / 2 / 8;
  width: 170px;
  height: 261px;
  padding-right: 65px;
}

@media (width <= 1023.98px) {
  .footer__contacts {
    border: none;
    grid-area: 1 / 4 / 2 / 6;
    width: 220px;
    padding-right: 0;
  }
}

@media (width <= 767.98px) {
  .footer__contacts {
    grid-area: 2 / 2 / 3 / -1;
    width: 128px;
    height: 189px;
  }
}

.footer__contacts-title {
  margin: 0 0 20px;
}

.footer__contacts-link {
  color: inherit;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: block;
}

.footer__contacts-link:hover {
  color: #1a5a4c;
}

.footer__nav {
  flex-direction: row;
  grid-area: 1 / 8 / 2 / 12;
  justify-content: flex-start;
  gap: 30px;
  width: 370px;
  display: flex;
}

@media (width <= 1023.98px) {
  .footer__nav {
    border-top: 1px solid #1a5a4c;
    grid-area: 2 / 2 / 3 / 6;
    width: 471px;
    margin-bottom: 120px;
    padding-top: 64px;
  }
}

@media (width <= 767.98px) {
  .footer__nav {
    grid-area: 3 / 1 / 4 / -1;
    gap: 20px;
    width: 280px;
    margin-bottom: 80px;
    padding-top: 40px;
  }
}

.footer__nav-list {
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;
  width: 170px;
  height: 62px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

@media (width <= 1023.98px) {
  .footer__nav-list {
    width: 220px;
  }
}

@media (width <= 767.98px) {
  .footer__nav-list {
    width: 130px;
    height: 42px;
  }
}

.footer__nav-link {
  color: #0f0e08;
  line-height: 1;
  text-decoration: none;
  position: relative;
}

.footer__nav-link:after {
  content: "";
  transform-origin: 0;
  background-color: #0f0e08;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: scale(0);
}

.footer__nav-link:hover:after {
  transform: scale(1);
}

